  addItem(i: number, j: number, k: number) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    // console.log(data.items);
    data.dialog = Object.assign({}, data.dialog, {
      isShow: true,
      width: 720,
      persistent: true,
      type: 'add',
      title: '舔加用户',
      inButton: [
        {
          label: '取消'
        },
        {
          label: '保存',
          type: 'add'
        }
      ],
      index: {
        i,
        j,
        k
      },
      editedItem: {
        avatar: {},
        name: '',
        phoneNumber: '',
        email: '',
        department: '',
        job: '',
        skills: [],
        address: ''
      }
    });
  }

  editItem(i: number, j: number, k: number, item: object) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    const index = data.items.indexOf(item);

    data.dialog = Object.assign({}, data.dialog, {
      isShow: true,
      width: 720,
      persistent: true,
      type: 'edit',
      title: '编辑用户',
      text: '',
      inButton: [
        {
          label: '取消'
        },
        {
          label: '保存',
          type: 'edit'
        }
      ],
      index: {
        i,
        j,
        k
      },
      editedIndex: index,
      editedItem: Object.assign({}, item)
    });
  }

  deleteItem(i: number, j: number, k: number, item: object) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    const index = data.items.indexOf(item);

    data.dialog = Object.assign({}, data.dialog, {
      isShow: true,
      type: 'del',
      persistent: false,
      width: 440,
      title: '确定删除？',
      text: '删除用户后不可恢复，是否确定要删除？',
      inButton: [
        {
          label: '取消'
        },
        {
          label: '确定',
          type: 'del'
        }
      ],
      index: {
        i,
        j,
        k
      },
      editedIndex: index
    });
    // console.log(data);
    // confirm('Are you sure you want to delete this item?') && data.items.splice(index, 1);
  }

  delMultiItem(i: number, j: number, k: number, items: object) {
    // console.log(typeof items); // object
    // console.log(items.length);
    // console.log(items);

    // if (items.length) {
    if (items) {
      const data = this.dataTablesItems[i].dataTables[j].items[k];
      data.dialog = Object.assign({}, data.dialog, {
        isShow: true,
        type: 'delMulti',
        persistent: false,
        width: 440,
        title: '确定删除多行数据？',
        text: '删除用户后不可恢复，是否确定要删除？',
        inButton: [
          {
            label: '取消'
          },
          {
            label: '确定',
            type: 'delMulti'
          }
        ],
        index: {
          i,
          j,
          k
        },
        delMultiItem: items
      });
    }
  }

  close(dialog: any) {
    const data = this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k];
    data.dialog.isShow = false;
    setTimeout(() => {
      data.dialog = Object.assign({}, data.dialog, {
        isShow: false,
        type: '',
        persistent: false,
        title: '',
        text: '',
        inButton: [],
        index: {},
        editedIndex: -1,
        editedItem: Object.assign({}, data.dialog.defaultItem),
        delMultiItem: []
      });
      setTimeout(() => {
        this.resetValidation();
      });
    }, 100);
  }

  dialog(action: string, dialog: any) {
    const data = this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k];
    const newItem = Object.assign({}, dialog.editedItem, {
      id: dialog.nextId,
      avatar: {
        icon: 'mdi-account',
        size: 32,
        color: 'borderColor4'
      }
    });
    switch (action) {
      case 'add':
        new Promise((resolve, reject) => {
          if (this.validate()) {
            resolve();
          }
        }).then(() => {
          data.items.push(newItem);
          dialog.nextId++;
          this.close(dialog);
        });
        break;

      case 'edit':
        if (this.validate()) {
          // data.items[dialog.editedIndex] = Object.assign({}, data.items[dialog.editedIndex], dialog.editedItem); // 利用索引直接设置一个数组项时不是响应式的
          // Object.assign(data.items[dialog.editedIndex], dialog.editedItem);
          this.$set(data.items, dialog.editedIndex, dialog.editedItem);

          // console.log(Object.assign({}, dialog.editedItem));
          // console.log(this.dataTablesItems[dialog.index.i].dataTables[dialog.index.j].items[dialog.index.k].items[dialog.editedIndex]);

          this.close(dialog);
        }
        break;

      case 'del':
        data.items.splice(dialog.editedIndex, 1);
        data.selected = []; // 删除后需要初始化数据表格双向绑定的值，不然无法禁用删除按钮
        this.close(dialog);
        break;

      case 'delMulti':
        dialog.delMultiItem.forEach((element: any) => {
          const index = data.items.findIndex((value: any) => {
            return value.id === element.id;
          });
          if (index > -1) {
            data.items.splice(index, 1);
          }
        });
        data.selected = []; // 删除后需要初始化数据表格双向绑定的值，不然无法禁用删除按钮
        this.close(dialog);
        break;

      default:
        this.close(dialog);
    }
  }

  validate() {
    if (this.$refs.form[0].validate()) {
      // this.snackbar = true;
      return true;
    }
  }

  resetValidation() {
    this.$refs.form[0].resetValidation();
  }

  reset() {
    this.$refs.form.reset();
  }

  initDate(i: number, j: number, k: number) {
    const data = this.dataTablesItems[i].dataTables[j].items[k];
    data.loading = true;
    setTimeout(() => {
      data.items = [].concat(data.initItems);
      data.loading = false;
    }, 1000);
    // console.log(data.items.length);
  }

  created() {
    const data = this.dataTablesItems[0].dataTables[0].items[0];
    data.loading = true;
    setTimeout(() => {
      data.items = [].concat(data.initItems);
      data.loading = false;
    }, 1000);
  }
}
